#about{
    min-height: 100vh;
    /* display: inline-flex;
    flex-flow: column wrap; */
    /* align-items: center; */

    flex-wrap: wrap;
    /* background-color: #fefcfb; */
    
  
}

.title {
    margin-top: 20px;
    height: 100px;
    color: #fefcfb;
    background-color:  #0a1128;
    display: flex;
    align-items: center;
    justify-items: center;
    font-size: 20pt;
}

.title h3{
    margin-left: 25pt;
    font-size: 1.5em;
    letter-spacing: 4pt;
  
}

.about-content {
    /* color: #353535; */
    padding-top: 50px;
    margin: 60px 70px 20px 70px;
    flex-wrap: wrap;
    
    display: flex;
  
   
    
    /* justify-content: space-evenly;
    align-items: stretch; */
}

#about-text{
   
    margin-right: 25px;
    max-width: 60%;
    color: #fefcfb;
    font-size: 25px;
}



.about-content li {
    
    font-family: 'Source Code Pro', monospace;
    color: #0466c8;
    text-align: center;
    padding: .75em;
    list-style-type: none;
}

.about-content ul{
  
    display: flex 2;

    column-count: 2;
    min-width: 230px




   
    
}





